 @include tablet {
   .seccion-inferior {
     display: grid;
     grid-template-columns: 2fr 1fr;
     column-gap: 2rem;
   }
 }

 .entrada-blog {
   margin-bottom: 2rem;

   @include tablet {
     display: grid;
     grid-template-columns: 1fr 2fr;
     column-gap: 3rem;
   }

   :last-of-type {
     margin-bottom: 0;
   }
 }

 .texto-entrada {
   line-height: 1.0;

   a {
     color: $negro;
     margin-top: 2rem;
     display: block;

     @include tablet {
       margin-top: 0;
     }

   }

   h4 {
     margin: 0;

     &::after {
       content: "";
       display: block;
       width: 15rem;
       height: 0.3rem;
       background-color: $verde;
       margin-top: 1rem;
     }
   }

   p {
     line-height: 1.5;
   }
 }

 .informacion-meta {
   span {
     color: $amarillo;
   }
 }