
    .testimonial {
        background-color: $verde;
        font-size: 2.2rem;
        padding: 1.8rem;
        color: $blanco;
        border-radius: 2rem;

        blockquote{
            padding-left: 5rem;
            position: relative;

            &::before{
                position: absolute;
                content: "";
                background-image: url(../img/comilla.svg);
                background-repeat: no-repeat;
                width: 6rem;
                height: 8rem;
                display: block;
                left: -2rem;
            }
        }
        p {
            text-align: right;
            color: $blanco;
        }       
    }
