/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.4rem;
  line-height: 1.6;
}

p {
  color: #000000;
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 110rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.4rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo-block {
  background-color: #E08709;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: rgb(189.6789699571, 114.3154506438, 7.6210300429);
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-amarillo:hover {
  background-color: rgb(189.6789699571, 114.3154506438, 7.6210300429);
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(90.2084745763, 141.3, 0);
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-verde-block:hover {
  background-color: rgb(90.2084745763, 141.3, 0);
  cursor: pointer;
}

.boton-rojo {
  background-color: #71B100;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-rojo:hover {
  background-color: rgb(90.2084745763, 141.3, 0);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(136, 5, 5);
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
}
.boton-rojo-block:hover {
  background-color: rgb(101.5659574468, 3.7340425532, 3.7340425532);
  cursor: pointer;
}

.dark-mode {
  background-color: #333333;
}
.dark-mode p,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario p,
.dark-mode .formulario legend {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #e1e1e1;
  color: 900;
  border: solid 1px solid #e1e1e1;
}

/** UI **/
.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 2;
  max-width: 50rem;
  padding: 0 0 12rem 0;
  border: 0;
  margin: 0;
}

.derecha {
  display: flex; /* Usa flexbox para organizar los elementos */
  flex-direction: column; /* Coloca los elementos en columna */
  align-items: center; /* Centra horizontalmente los elementos */
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end; /* En pantallas grandes, alinea a la derecha */
  }
}
.derecha .dark-mode-boton {
  width: 3rem; /* Tamaño del botón de modo oscuro */
  margin-top: 1rem;
  margin-bottom: 2rem;
  filter: invert(50%); /* Invierte parcialmente los colores (efecto gris) */
}
.derecha .dark-mode-boton:hover {
  cursor: pointer; /* Cambia el cursor al pasar por encima */
  filter: invert(100%); /* Invierte completamente los colores (efecto blanco) */
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0; /* Elimina margen superior en pantallas grandes */
  }
}

.barra {
  display: flex; /* Coloca el título y la navegación en línea */
  flex-direction: column; /* En móviles, apila los elementos en columna */
  align-items: center; /* Centra el contenido */
  padding-top: 3rem; /* Añade espacio superior */
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row; /* En pantallas grandes, coloca los elementos en fila */
    justify-content: space-between; /* Separa el título y la navegación */
    align-items: flex-end; /* Alinea verticalmente al final */
  }
}

.navegacion {
  visibility: hidden; /* Oculta la navegación por defecto */
  opacity: 0; /* La hace transparente */
  height: 0;
  transition: visibility 1s ease-in-out, opacity 0.5s linear, height 0.2s linear; /* Transición suave al mostrarse */
}
@media (min-width: 768px) {
  .navegacion {
    display: flex; /* En escritorio, muestra la navegación en línea */
    align-items: center; /* Centra los enlaces verticalmente */
    margin-left: 2rem;
    visibility: visible; /* Hace visible la navegación */
    opacity: 1; /* Totalmente visible */
  }
}
.navegacion a {
  color: #FFFFFF; /* Color del texto */
  text-decoration: none; /* Quita el subrayado */
  font-size: 1.4rem; /* Tamaño de texto para móviles */
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem; /* Espacio entre enlaces */
    font-size: 1.8rem; /* Aumenta el tamaño en escritorio */
  }
  .navegacion a:last-of-type {
    margin-right: 0; /* El último enlace no tiene margen derecho */
  }
}
.navegacion a:hover {
  color: #E08709;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none; /* Se oculta en pantallas grandes */
  }
}
.mobile-menu img {
  width: 5rem; /* Tamaño del icono del menú */
}

.mostrar {
  visibility: visible; /* Hace visible el menú oculto */
  opacity: 1; /* Totalmente opaco (efecto vidible) */
  height: auto;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
  margin: 0;
  left: 2rem;
}
.footer a {
  color: #FFFFFF;
  text-decoration: none;
  gap: 1rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin: 10rem 0 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .footer .copyright {
    margin: 2rem 0 0;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #e1e1e1;
  border: rgb(212.25, 212.25, 212.25) solid 1px;
}
.anuncio .contenido-anuncio {
  padding: 2.5rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-size: 2.2rem;
  font-weight: bold;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 1rem;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 0.5rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
  margin: 0;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-weight: 900;
}
.imagen-contacto p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 3rem;
  }
}
.entrada-blog :last-of-type {
  margin-bottom: 0;
}

.texto-entrada {
  line-height: 1;
}
.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.3rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada p {
  line-height: 1.5;
}

.informacion-meta span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.2rem;
  padding: 1.8rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  margin-top: 1rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  margin: 1rem 0 0 0;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 12rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario .forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

/** INTERNAS **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}

.prueba {
  text-align: center;
  text-transform: uppercase;
  padding: 0.5rem;
  margin: 0.5rem 0;
  color: white;
}
.prueba.error {
  background-color: rgb(136, 5, 5);
}
.prueba.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
  color: white;
}
table.propiedades thead th {
  padding: 1.5rem;
}
table.propiedades .imagen-tabla {
  display: block;
  margin: 0 auto;
  width: 15rem;
}
table.propiedades tbody {
  text-align: center;
}

.imagen-small {
  width: 25rem;
}